* {margin: 0; padding: 0;
    box-sizing: border-box;}
  
    .wrapper {
      margin: auto;
      width: 50vw;
      position: relative;
      border-radius: 50px;
    }
    
    header {
      color: white;
      background-color: #4044c9;
      padding: 100px 10px;
      font-weight: bold;
      border-bottom-left-radius: 50%;
      border-bottom-right-radius: 50%;
      box-shadow: 1px 5px 5px #c9cbefb2;
      top: 0;
      text-align: center;
      font-size: 2rem;
    }
    
    .list {
    background-color: white;
    border: 1px solid #eeedfc;
    padding: 20px 30px;
    border-bottom: 1px solid lightgray;
    display: flex;
    flex-wrap: wrap;
    /* border: 1px solid red; */
    border-radius: 50px;
    margin-top: 10px;
    box-shadow: 1px 5px 5px #c9cbefb2;
    transition: .5s;

  }

  .list:hover {
    background-color: #4044c9;
    color: white;
  }
  .listname {
    width: calc(100% - 100px);
    /* border: 1px solid red; */
    /* height: 10vh; */
    color: #4044c9;
    font-size: 1.8rem;
    transition: .5s;
  }

.listname:hover {
  color: white;
}


  .face {
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }


  .good {  width: 100px;}
  .good>span {cursor: pointer;}
  .date {
    width: 100%;
    font-size: 0.75rem;
    padding-top: 10px;
  }

  .bad>span {
    cursor: pointer;
  }
  
  .modal {
    background-color: #eee;
    padding: 20px;
    position: absolute;
    top:100px;
    right: 0;
    width: 50%;
  }
  
  .input {
    position: absolute;
    top: 100%;
    right: 50%;
    transform: translateX(50%);
    margin-top: 20px;
    }
  
  .delete {
    border: none;
    border-radius: 50px;
    background-color: none;
    width: 50px;
    height: 20px;
    background-color: #a3a0ff;
    color: white;
    margin-top: 15px;
  }

  .input_txt {
    display: flex;
  }

  .textA,
  .textB {
    border: 1px solid #4044c9;
    border-radius: 50px;
    height: 3vh;
    padding-left: 5px;
  }

  .textA {
    margin-right: 5px;
  }

  .input_btn {
    border: none;
    padding: 0.3rem 1rem;
    background-color: #a3a0ff;
    display: flex;
    text-align: center;
    color: white;
    border-radius: 50px;
    margin-top: 10px;
  }
  
  .inputbtn_wrap {
    width: 100%;
    display: flex;
    justify-content: center;
  }